<template>
  <base-card>
    <base-button
      @click="selected = 'stored-resources'"
      :mode="selected === 'stored-resources'? null:'flat'"
    >View Resources</base-button>
    <base-button
      @click="selected = 'add-resources'"
      :mode="selected === 'add-resources'? null:'flat'"
    >Add Resources</base-button>
  </base-card>
  <keep-alive>

  <component :is="selected"></component>

  </keep-alive>

  <!-- <stored-resources></stored-resources> -->
  <!-- <add-resources></add-resources> -->
  <!-- <div>{{stored}}</div> -->

</template>


<script>
// import AddResources from '../resources/AddResources.vue';
// import AddResources from "../resources/AddResources.vue";
import AddResources from "../resources/AddResources.vue";
import StoredResources from "../resources/StoredResources.vue";
export default {
  components: { StoredResources, AddResources },

  data() {
    return {
      selected: "stored-resources",
      stored: [
        {
          id: "1",
          title: "Official Guide",
          description: "The official Vue.js documentation",
          link: "https://vuejs.org",
        },
        {
          id: "2",
          title: "Google",
          description: "Goodle search",
          link: "https://google.com",
        },
      ],
    };
  },
  methods: {
    addLinks(title, description, link) {
      // console.log("哈哈"+link);
      const newobj = {
        id: new Date().toISOString(),
        title:title,
        description:description,
        link:link
      }
      // console.log(title);
      // console.log(newobj);
      this.stored.unshift(newobj)
    },
    deleteItem(id){
      const indexM = this.stored.findIndex(res => res.id === id)
      this.stored.splice(indexM,1)
    }
  },
  provide() {
    return {
      resources: this.stored,
      addLinks: this.addLinks,
      deleteItem:this.deleteItem
    };
  },
};
</script>