<template>
<div >


  <base-dialog
    v-if="!validInput"
    title="Invalid Input"
    @my-close="closeDlg"
  >
    <template #default>
      <p>Please fill every input box</p>
    </template>
    <template #actions> 
    <base-button @click="closeDlg">Okey</base-button> 
    </template>

</base-dialog>
  <base-card>
    <form @submit.prevent="submitObj">
      <div class="form-control">
        <label for="title" >Title</label>
        <input
          id="title"
          name="title"
          type="text"
          ref = "title"
        />

      </div>

      <div class=" form-control">
        <label for="description">Description</label>
        <textarea
          rows="3"
          id="description"
           ref="description"
          name="description"
        />

      </div>
      <div class=" form-control">
        <label for="link" >Link</label>
        <input type="url" ref = "link"/>
      </div>
      <base-button type="submit">Submit</base-button>
    </form>

  </base-card>
</div>

</template>
<script>
export default {
  data() {
    return { validInput: true };
  },
  inject: ["addLinks"],
  methods: {
    closeDlg(){this.validInput = true},
    submitObj() {
      const title = this.$refs.title.value;
      const description = this.$refs.description.value;
      const link = this.$refs.link.value;
      // console.log(link);

      if (
        title.trim() === "" ||
        description.trim() == "" ||
        link.trim() === ""
      ) {
        // alert("invalid input");
        this.validInput = false;
        // console.log("asd");
        return;
      }
      this.addLinks(title, description, link);
    },
  },
};
</script>

<style scoped>
label {
  font-weight: bold;
  display: block;
  margin-bottom: 0.5rem;
}

input,
textarea {
  display: block;
  width: 100%;
  font: inherit;
  padding: 0.15rem;
  border: 1px solid #ccc;
}

input:focus,
textarea:focus {
  outline: none;
  border-color: #3a0061;
  background-color: #f7ebff;
}

.form-control {
  margin: 1rem 0;
}
</style>